<!--预案详情-->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">预案详情</p>
      </van-col>
    </van-row>

    <van-row class="dt-content">
      <van-row class="dt-table">
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">预案编号：</van-col>
          <span class="dt-item-content">{{task.PlanCode}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">预案名称：</van-col>
          <span class="dt-item-content">{{task.PlanName}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">预案分类：</van-col>
          <span class="dt-item-content">{{task.PlanType}}</span>
        </van-row>

        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">预案内容：</van-col>
          <span class="dt-taskContent">{{task.PlanContent}}</span>
        </van-row>
        <!-- <van-row class="dt-top">
          <van-col class="dt-item-title">预案图片：</van-col>
        </van-row>
        <van-row gutter="10"
                 class="dt-pic">
          <van-col :span="8">
            <van-image width="89"
                       height="89"
                       :src="require('../../../assets/img/工单详情1.png')" />
          </van-col>
          <van-col :span="8">
            <van-image width="89"
                       height="89"
                       :src="require('../../../assets/img/工单详情2.png')" />
          </van-col>
          <van-col :span="8">
            <van-image width="89"
                       height="89"
                       :src="require('../../../assets/img/工单详情3.png')" />
          </van-col>
        </van-row> -->
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">预案联系人：</van-col>
          <span class="dt-item-content">{{task.PlanLinkPhone}}</span>
        </van-row>
      </van-row>
    </van-row>
  </van-row>
</template>
<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { planInformationSearch } from '@/api/planInformation';
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      task: {},
      value: '',
      PlanContent: '公司的公交卡是好的交给我大家理办法今日的开工kgis剧么偶v。',
    }
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () { },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    var come = this.$route.query.come

    if (come) {
      this.getPlanInfoDetailsType();
    } else {
      this.getPlanInfoDetailsId();
    }


  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 返回首页按钮
    Home () {
      this.$router.push({ name: 'Home' })
    },
    getPlanInfoDetailsId () {
      planInformationSearch({ ID: this.$route.query.ID }).then(response => {
        this.task = response.Data.ContentList[0]
      })
    },
    getPlanInfoDetailsType () {
      planInformationSearch({ PlanModule: this.$route.query.secunityType }).then(response => {
        this.task = response.Data.ContentList[0]
      })
    },
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang="scss" scoped>
@import '../../../styles/dimens';
//@import url(); 引入公共css类
$noticeBarHeight: px2rem(50);
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: 'SourceHanSansCN-Medium';
  color: #ecedf2;
}
.dt-content {
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
  overflow-y: auto;
  // background-image: url("../../../assets/img/工单背景.png");
  background-size: 100% 100%;
  background-attachment: fixed;
  background-position: 0 px2rem(74);
  .dt-table {
    float: left; // 为了清除父子元素外边距重叠
    width: 94%;
    margin: 0 3%;
    margin-top: px2rem(29);
    overflow-y: auto;
    .dt-item-title {
      width: px2rem(90);
      margin-left: px2rem(30);
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      color: #4c4c4c;
      font-weight: 500;
      line-height: px2rem(23);
      text-align: left;
    }
    .dt-item-content {
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      font-weight: 400;
      text-align: left;
      color: #707070;
      line-height: px2rem(23);
      width: px2rem(200);
      float: right;
      padding-right: px2rem(25);
    }
    .dt-taskContent {
      display: inline-block;
      vertical-align: top;
      //border: rgb(216, 216, 216) solid 1px;
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      font-weight: 400;
      text-align: left;
      color: #707070;
      line-height: px2rem(23);
      width: px2rem(195);
      // float: left;
      margin-right: px2rem(25);
    }
    .dt-top {
      margin-top: px2rem(7.5);
    }
    .dt-pic {
      margin-top: px2rem(7.5);
      padding: 0 px2rem(30);
      overflow-y: auto;
      .dt-img {
        //图片
        position: relative;
        width: px2rem(89);
        height: px2rem(89);
        float: left;
        padding-top: 50%; /*padding-bottom 都可以*/
        .van-image {
          //图片
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
</style>
