import myrequest from '@/utils/axios'

import { msapiOnline } from "./onLineBaseUrl";

export function planInformationSearch (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/tb_project_reserveplan/GetPageContentByConditions',
    method: 'post',
    data: params
  })
}

